/*问题*/

.part-wenti {
    background: url(assets/images/jingjiadt/yxtx/wenti.jpg)no-repeat center;
    background-size: cover;
}

.part-wenti .titlebox h1 {
    color: #fff;
    font-size: 6vw;
}

.wenti-item {
    margin-bottom: 0.15rem;
}

.wenti-lastitem {
    margin-bottom: 0;
}

.wenti-item p {
    font-size: 0.12rem;
    color: #fff;
    line-height: 0.3rem;
    float: left;
}

.wenti-item p:nth-of-type(1) {
    width: 72%;
    padding-left: 3%;
    border: 1px solid #5e5e66;
    border-right: none;
}

.wenti-item p:nth-of-type(2) {
    width: 24%;
    text-align: center;
    background: #fcc800;
    border: 1px solid #fcc800;
}


